
body {
  margin: 0;
  padding: 0;
}

.background {
  background-image: url("food1.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 100vh;
  width: 100vw;
}
.background2 {
  background-image: url("burger.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 100vh;
  width: 100vw;
}
.navbar {
  width: 100vw;
  position: absolute;
  top: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  background-color: rgba(0, 0, 0, 0);
  background-color: rgba(0, 0, 0, 0.3);
  border-bottom: 1px solid #000;
}
.navbar a {
  font-weight: lighter;
  text-decoration: none;
  margin: 20px 40px;
  color: #f7f0e2;
  font-size: 24px;
  font-variant: small-caps;
  letter-spacing: 2px;
}
.navbar a:hover {
  color: rgb(223, 181, 0);
}

.modal {
  font-weight: lighter;
}



